
import cookie from 'vux/src/tools/cookie/index.js'

import api from '../../assets/js/api'
import {getParams, weChatPay} from '../../assets/js/util'
export default {
components: {},
data () {
return {
code: getParams()['code'] || '',
order_id: getParams()['order_id'] || ''
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
goPay(){
this.showLoading('正在支付')
this.$http.post(api.pay, {
code: this.code,
order_id: this.order_id,
url: location.href.split('?')[0]
}).then(res => {
this.hideLoading()
this.weChatPay()

}).catch(e => {
this.hideLoading()
})
},
weChatPay(){
let _this = this
this.$wechat.config({
debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
appId: '', // 必填，公众号的唯一标识
timestamp: 12123, // 必填，生成签名的时间戳
nonceStr: '', // 必填，生成签名的随机串
signature: '',// 必填，签名
jsApiList: ['chooseWXPay'] 
})
this.$wechat.ready(() => {
this.$wechat.chooseWXPay({
timestamp: 0, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
nonceStr: '', // 支付签名随机串，不长于 32 位
package: '', // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
signType: '', // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
paySign: '', // 支付签名
success: function (res) {

/*分邮寄和自提支付成功*/
_this.$router.push({
path: `/postSuc?order_id=${this.order_id}`
})
},
fail: function (e) {
_this.layer(e)
}
});
})
}
},
mounted() {

document.getElementsByTagName('title')[0].textContent = '正在支付';
}
}



